<template>
  <div class="my">
    <!-- 背景图 -->
    <div class="my-bg" :style="{ backgroundImage: `url('${userInfo.userBg}')` }">
      <van-uploader class="upload-box" :after-read="uploadBg" />
    </div>
    <!-- 我的信息 -->
    <div class="my-info">
      <div class="clearfix">
        <!-- 头像 -->
        <div class="fl my-img">
          <img class="auto-img" :src="userInfo.userImg" alt="" />
          <van-uploader class="upload-box" :after-read="uploadUserImg" />
        </div>
        <div class="fl my-info-box">
          <div class="user-name">{{ userInfo.nickName }}</div>
          <div class="user-desc">
            {{ userInfo.desc == "" ? "这家伙很懒，什么也没有留下！" : userInfo.desc }}
          </div>
        </div>
      </div>
    </div>

    <!-- 我的订单 -->
    <div class="my-order">
      <div class="order-title clearfix">
        <div class="title-name fl">我的订单</div>
        <div class="title-all fr">全部订单 ＞</div>
      </div>
      <div class="order-box clearfix">
        <div class="order-icon fl" v-for="(item, index) in orderData" :key="index">
          <div class="img-box"><img class="auto-img" :src="item.url" alt="" /></div>
          <div class="order-name">{{ item.name }}</div>
        </div>
      </div>
    </div>
    <!-- 信息列表 -->
    <div class="list-box">
      <van-cell
        v-for="(item, index) in listData"
        :key="index"
        :title="item.title"
        is-link
        @click="goPage(item.name)"
      />
    </div>
    <!-- 当前版本 -->
    <div class="edition-box clearfix">
      <div class="fl edition-name">当前版本</div>  
      <div class="fl edition-number">3.5.9.11</div> 
    </div>
  </div>
</template>

<script>
import "../assets/less/my.less";
// 导入axios
import axios from "axios";
import VueAxios from "vue-axios";
export default {
  name: "My",
  data() {
    return {
      //用户信息
      userInfo: {},

      orderData: [
        {
          url: require("../assets/images/awaitPayment.png"),
          name: "代付款",
        },
        {
          url: require("../assets/images/Consignment.png"),
          name: "代发货",
        },
        {
          url: require("../assets/images/Delivered.png"),
          name: "已发货",
        },
        {
          url: require("../assets/images/awaitEvaluate.png"),
          name: "待评价",
        },
      ],
      listData: [
        {
          title: "个人资料",
          name: "Account",
        },
        {
          title: "门客服务",
          name: "Service",
        },
        {
          title: "电商服务",
          name: "OnlineRetailers",
        },
        {
          title: "用户反馈",
          name: "Feedback",
        },
      ],
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("token");

      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      // 设置当前基础路径
      axios.defaults.baseURL = "http://www.kangliuyong.com:10002";
      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            //token检验无效,则跳到登录页面
            this.$router.push({ name: "Login" });
          } else if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //上传背景
    uploadBg(file) {
      //
      //允许文件类型
      let type = ["gif", "png", "jpg", "jpeg"];

      //允许最大文件大小 1MB
      let size = 1;

      //判断文件类型
      let fileType = file.file.type.split("/")[1];
      if (type.indexOf(fileType) === -1) {
        this.$toast(`文件类型只支持${type.join(",")}`);
        return;
      }

      //判断文件大小 B, 1024B = 1KB, 1024KB = 1MB
      let fileSize = file.file.size / 1024 / 1024;

      if (fileSize > size) {
        this.$toast(`文件允许最大不能超过${size}MB`);
        return;
      }

      //处理base64的标记data:image/jpeg;base64,
      let base64 = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, "");
      //

      //发起请求
      let tokenString = localStorage.getItem("token");

      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      // 设置当前基础路径
      axios.defaults.baseURL = "http://www.kangliuyong.com:10002";

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType: fileType,
          serverBase64Img: base64,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 700) {
            //token检验无效,则跳到登录页面
            this.$router.push({ name: "Login" });
          } else if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          }

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //上传用户头像
    uploadUserImg(file) {
      //允许文件类型
      let type = ["gif", "png", "jpg", "jpeg"];

      //允许最大文件大小 1MB
      let size = 1;

      //判断文件类型
      let fileType = file.file.type.split("/")[1];
      if (type.indexOf(fileType) === -1) {
        this.$toast(`文件类型只支持${type.join(",")}`);
        return;
      }

      //判断文件大小 B, 1024B = 1KB, 1024KB = 1MB
      let fileSize = file.file.size / 1024 / 1024;

      if (fileSize > size) {
        this.$toast(`文件允许最大不能超过${size}MB`);
        return;
      }

      //处理base64的标记data:image/jpeg;base64,
      let base64 = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, "");
      //

      //发起请求
      let tokenString = localStorage.getItem("token");

      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      // 设置当前基础路径
      axios.defaults.baseURL = "http://www.kangliuyong.com:10002";

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType: fileType,
          serverBase64Img: base64,
        },
      })
        .then((result) => {
          if (result.data.code == 700) {
            //token检验无效,则跳到登录页面
            this.$router.push({ name: "Login" });
          } else if (result.data.code == "H001") {
            this.userInfo.userImg = result.data.userImg;
          }

          this.$toast(result.data.msg);
          this.$toast.clear();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(result.data.msg);
        });
    },
    goPage(name) {
      this.$router.push({ name });
    },
  },
};
</script>
